import { makeStyles, Typography, Grid } from '@material-ui/core'
import React from 'react'
import GalleryCaraousel from './GalleryCaraousel';
import GalleryVideos from './GalleryVideos';
import ParallaxHeader from './ParallaxHeader'

const useStyles = makeStyles({
    root:{
        position:"relative",
        padding:"5em 0em",
        background: "#4a4a4a "
    },
    header:{
        position:"absolute",
        height:"100%",
        width:"auto",
        zIndex:1,
        left:0,
        textAlign:"center",'@media only screen and (max-width : 1199px)': {
            left:"auto",
        },
    },
    carousel:{
        position:"relative",
        zIndex:2,
        margin:"5em 0em",
    },
    videos:{
        position:"relative",
        zIndex:2,
    }
});


const Gallery = () => {
    const classes = useStyles()
    return (
        <Grid container xs={12} className={classes.root} justify="center" alignContent="center">
            <Grid container xs={12} justify="left" className={classes.header}>
                <span>
                    <ParallaxHeader
                        title="Gallery"
                        duration="670"
                        to="6.8em" 
                    /> 
                </span>
            </Grid>

                <Grid container xs={10} sm={8} lg={6} className={classes.carousel} justify="center" alignContent="center" gutterBottom> 
                    <GalleryCaraousel/>
                </Grid>

                <Grid container xs={12}  sm={11} lg={9} className={classes.videos} justify="center" alignContent="center">
                    <GalleryVideos/>
                </Grid>
        </Grid>
    )
}

export default Gallery
