import { Grid, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import FacebookIcon from '@material-ui/icons/Facebook';
import YouTubeIcon from '@material-ui/icons/YouTube';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';

import React from 'react'

const useStyles = makeStyles({
    root:{
        width:"100%",
        background:"orange",
        padding:"5em"
    },
    icons:{
        marginTop:"2em",
        display:"flex",
        justifyContent:"space-around"
    }
})

const Footer = () => {
    const classes=useStyles();
    return (
        <Grid xs={12} className={classes.root} container justify="center">
            <Grid xs={12} gutterBottom>
                <Typography variant="h2">
                    Hello
                </Typography>
            </Grid>
            <Grid xs={12} sm={8} lg={5} container justify="center" className={classes.icons}>
                <FacebookIcon style={{ fontSize: 46 }}/>
                <InstagramIcon style={{ fontSize: 46 }}/>
                <YouTubeIcon style={{ fontSize: 46 }}/>
                <TwitterIcon style={{ fontSize: 46 }}/>
            </Grid>
        </Grid>
    )
}

export default Footer
