import './App.css';
import Layout from './Component/Layout';
import 'bootstrap/dist/css/bootstrap.min.css';
import { createMuiTheme, ThemeProvider} from '@material-ui/core'

function App() {
  const theme = createMuiTheme({
    palette:{
        primary:{
            main:"#605F5D"
        },
        secondary:{
            main:"#FEF9EE"
        },
        error:{
            main:"#ffffff"
        }
    }
});
  return (
    <div className="App">
      <ThemeProvider theme={theme}>
        <Layout/>
      </ThemeProvider>
    </div>
  );
}

export default App;
